import React from 'react';
// nodejs library to set properties for components
import PropTypes from 'prop-types';
// react components for routing our app without refresh
import { Link } from 'react-router-dom';

// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Icon from '@material-ui/core/Icon';

// @material-ui/icons
import Apps from '@material-ui/icons/Apps';
import Dns from '@material-ui/icons/Dns';
import Build from '@material-ui/icons/Build';
import ListIcon from '@material-ui/icons/List';
import People from '@material-ui/icons/People';
import Assignment from '@material-ui/icons/Assignment';
import MonetizationOn from '@material-ui/icons/MonetizationOn';
import Chat from '@material-ui/icons/Chat';
import Call from '@material-ui/icons/Call';
import ViewCarousel from '@material-ui/icons/ViewCarousel';
import AccountBalance from '@material-ui/icons/AccountBalance';
import ArtTrack from '@material-ui/icons/ArtTrack';
import ViewQuilt from '@material-ui/icons/ViewQuilt';
import LocationOn from '@material-ui/icons/LocationOn';
import Fingerprint from '@material-ui/icons/Fingerprint';
import AttachMoney from '@material-ui/icons/AttachMoney';
import Store from '@material-ui/icons/Store';
import AccountCircle from '@material-ui/icons/AccountCircle';
import PersonAdd from '@material-ui/icons/PersonAdd';
import Layers from '@material-ui/icons/Layers';
import LineStyle from '@material-ui/icons/LineStyle';
import Error from '@material-ui/icons/Error';
import Android from '@material-ui/icons/Android';
import MobileFriendly from '@material-ui/icons/MobileFriendly';

// core components
import CustomDropdown from 'components/CustomDropdown/CustomDropdown.jsx';
import Button from 'components/CustomButtons/Button.jsx';

import headerLinksStyle
  from 'assets/jss/material-kit-pro-react/components/headerLinksStyle.jsx';

import * as ROUTES from 'constants/routes';

function HeaderLinks({ ...props }) {
  const easeInOutQuad = (t, b, c, d) => {
    t /= d / 2;
    if (t < 1) return c / 2 * t * t + b;
    t--;
    return -c / 2 * (t * (t - 2) - 1) + b;
  };

  const smoothScroll = (e, target) => {
    if (window.location.pathname === '/') {
      const isMobile = navigator.userAgent.match(
        /(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i,
      );
      if (isMobile) {
        // if we are on mobile device the scroll into view will be managed by the browser
      } else {
        e.preventDefault();
        debounceScroll(target);
      }
    }
  };
  const debounceScroll = (target) => {
    const top = (window.pageYOffset || document.scrollTop || 0) - (document.clientTop || 0);
    const targetScroll = document.getElementById(target) && document.getElementById(target).parentNode;
    if (targetScroll) {
      const diff = targetScroll.offsetTop - top;

      if (diff > 1) {
        scrollGo(document.documentElement, targetScroll.offsetTop, 1250);
      }
    }
  };

  const scrollGo = (element, to, duration) => {
    const start = element.scrollTop;
    const change = to - start;
    let currentTime = 0;
    const increment = 20;

    const animateScroll = () => {
      currentTime += increment;
      const val = easeInOutQuad(currentTime, start, change, duration);
      element.scrollTop = val;
      if (currentTime < duration) {
        setTimeout(animateScroll, increment);
      }
    };
    animateScroll();
  };

  if (window.location.hash === '#contact-us') {
    setTimeout(() => {
      debounceScroll('contacts');
    }, 0);
  } else if (window.location.pathname === '/') {
    const top = (window.pageYOffset || document.scrollTop || 0) - (document.clientTop || 0);
    if (top !== 0) {
      scrollGo(document.documentElement, 0, 1250);
    }
  }

  const {
    classes,
    dropdownHoverColor,
    hasUserSession,
    isUserAdmin,
  } = props;
  return (
    <List className={`${classes.list} ${classes.mlAuto}`}>
      { hasUserSession && (
        <ListItem className={classes.listItem}>
          <Link to={ROUTES.HOME} className={classes.navLink}>
            <AccountBalance className={classes.icons} />
            Home
          </Link>
        </ListItem>
      )}
      <ListItem className={classes.listItem}>
        <ListItem className={classes.listItem}>
          <Link to={ROUTES.PRODUCTS} className={classes.navLink}>
            <Apps className={classes.icons} />
            Products
          </Link>
        </ListItem>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link to={ROUTES.PRICING} className={classes.navLink}>
          <AttachMoney className={classes.icons} />
          Pricing
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link to={ROUTES.ABOUT_US} className={classes.navLink}>
          <AccountBalance className={classes.icons} />
          About Us
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <a
          href="/#contact-us"
          className={classes.navLink}
          onClick={(e) => smoothScroll(e, 'contacts')}
        >
          <Call className={classes.icons} />
          Contact Us
        </a>
      </ListItem>
      { isUserAdmin && (
        <ListItem className={classes.listItem}>
          <Link to={ROUTES.ADMIN} className={classes.navLink}>
            <AccountCircle className={classes.icons} />
            Admin
          </Link>
        </ListItem>
      ) }
      { !hasUserSession && (
        <ListItem className={classes.listItem}>
          <Link to={ROUTES.SIGN_IN} className={classes.navLink}>
            <Fingerprint className={classes.icons} />
            SignIn
          </Link>
        </ListItem>
      ) }
      { !hasUserSession && (
        <ListItem className={classes.listItem}>
          <Link to={ROUTES.SIGN_UP} className={classes.navLink}>
            <PersonAdd className={classes.icons} />
            SignUp
          </Link>
        </ListItem>
      ) }
      <ListItem className={`${classes.listItem} ${classes.marginRight5}`}>
        <Button
          href="https://www.creative-tim.com/product/material-kit-pro-react"
          color={window.innerWidth < 960 ? 'info' : 'white'}
          target="_blank"
          className={classes.navButton}
          round
        >
          <MobileFriendly className={classes.icons} />
          IOS
        </Button>
      </ListItem>

      <ListItem className={classes.listItem}>
        <Button
          href="https://www.creative-tim.com/product/material-kit-pro-react"
          color={window.innerWidth < 960 ? 'info' : 'white'}
          target="_blank"
          className={classes.navButton}
          round
        >
          <Android className={classes.icons} />
          Android
        </Button>
      </ListItem>
    </List>
  );
}

HeaderLinks.defaultProps = {
  hoverColor: 'primary',
  hasUserSession: false,
  isUserAdmin: false,
};

HeaderLinks.propTypes = {
  dropdownHoverColor: PropTypes.oneOf([
    'dark',
    'primary',
    'info',
    'success',
    'warning',
    'danger',
    'rose',
  ]),
  hasUserSession: PropTypes.bool,
  isUserAdmin: PropTypes.bool,
};

export default withStyles(headerLinksStyle)(HeaderLinks);
