import React, { useEffect } from 'react';
import { withRouter } from 'react-router-dom';

import Header from 'components/Header/Header.jsx';
import HeaderLinks from 'components/Header/HeaderLinks.jsx';
import { AuthUserContext } from 'pages/Session';
import SignOutButton from 'pages/SignOut';
import * as ROUTES from 'constants/routes';
import * as ROLES from 'constants/roles';

import useGlobal from 'store';

const Navigation = ({ location, history }) => {
  const [globalState, globalActions] = useGlobal();
  const { pathname } = location;

  const isPositionAbsolute = pathname === ROUTES.SIGN_IN || pathname === ROUTES.SIGN_UP;

  useEffect(() => {
    const unlisten = history.listen(() => {
      globalActions.root.clear();
    });
    return () => {
      unlisten();
    };
  });
  return (
    <AuthUserContext.Consumer>
      {authUser => (authUser
        ? null
        : <NavigationNonAuth absolute={isPositionAbsolute} />)}
    </AuthUserContext.Consumer>
  );
};

const NavigationAuth = ({ authUser }) => (
  <Header
    color="transparent"
    brand="Outlit PRO"
    links={(
      <HeaderLinks
        dropdownHoverColor="info"
        hasUserSession
        isUserAdmin={!!authUser.roles[ROLES.ADMIN]}
      />
    )}
    fixed
    changeColorOnScroll={{
      height: 300,
      color: 'info',
    }}
  />
);
  {/* <Menu pointing secondary>

    <Menu.Item name="Landing" as={Link} to={ROUTES.LANDING} />
    <Menu.Item name="home" as={Link} to={ROUTES.HOME} />
    <Menu.Item name="CreateEvent" as={Link} to={ROUTES.CREATE_EVENT} />
    <Menu.Item name="Account" as={Link} to={ROUTES.ACCOUNT} />
    {!!authUser.roles[ROLES.ADMIN] &&
      <Menu.Item name="Admin" as={Link} to={ROUTES.ADMIN} />}
    <SignOutButton />
  </Menu> */}

const NavigationNonAuth = ({ absolute }) => (
  <Header
    absolute={absolute}
    color="transparent"
    brand="Outlit PRO"
    links={<HeaderLinks dropdownHoverColor="info" />}
    fixed={!absolute}
    changeColorOnScroll={{
      height: 300,
      color: 'info',
    }}
  />
);

export default withRouter(Navigation);
