import React from 'react';
// nodejs library that concatenates classes
import classNames from 'classnames';
// @material-ui/core components
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import withStyles from '@material-ui/core/styles/withStyles';
// @material-ui/icons
// core components
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import CustomInput from 'components/CustomInput/CustomInput.jsx';
import Button from 'components/CustomButtons/Button.jsx';

import contactStyle from 'assets/jss/material-kit-pro-react/views/aboutUsSections/contactStyle.jsx';
import { FormDataToJSON, verifyEmail } from 'utils/form';

class SectionContact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      specialitySelect: '1',
      name: '',
      nameState: false,
      emailState: false,
      designationState: false,
    };
  }

  handleSpeciality = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value, [`${name}State`]: !!value });
  };

  onSubmit(event) {
    const payload = FormDataToJSON(document.querySelector('#contact'));
    const { name, email, designation } = payload;
    if (payload.name && payload.email && payload.designation && verifyEmail(email)) {
      this.props.sendMessage(payload);
    } else {
      this.setState({
        nameState: !name,
        emailState: !email || !verifyEmail(email),
        designationState: !designation,
      });
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.aboutUs}>
        <GridContainer>
          <GridItem
            md={8}
            sm={8}
            className={classNames(classes.mrAuto, classes.mlAuto)}
          >
            <h2 className={classNames(classes.title, classes.textCenter)}>
              Want to work with us?
            </h2>
            <h4 className={classNames(classes.description, classes.textCenter)}>
              Divide details about your product or agency work into parts. Write
              a few lines about each one and contact us about any further
              collaboration. We will get back to you in a couple of hours.
            </h4>
            <form id="contact">
              <GridContainer>
                <GridItem md={4} sm={4}>
                  <CustomInput
                    labelText="Your name"
                    error={this.state.nameState}
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      name: 'name',
                    }}
                  />
                </GridItem>
                <GridItem md={4} sm={4}>
                  <CustomInput
                    labelText="Your email"
                    error={this.state.emailState}
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      name: 'email',
                    }}
                  />
                </GridItem>
                <GridItem md={4} sm={4}>
                  <FormControl
                    fullWidth
                    error={this.state.designationState}
                    className={`${classes.selectFormControl} ${
                      classes.selectUnderlineRoot
                    }`}
                  >
                    <Select
                      MenuProps={{
                        className: classes.selectMenu,
                      }}
                      classes={{
                        select: classes.select,
                      }}
                      value={this.state.specialitySelect}
                      onChange={this.handleSpeciality}
                      inputProps={{
                        name: 'designation',
                        id: 'speciality-select',
                      }}
                    >
                      <MenuItem
                        disabled
                        classes={{
                          root: classes.selectMenuItem,
                        }}
                      >
                        Speciality
                      </MenuItem>
                      <MenuItem
                        classes={{
                          root: classes.selectMenuItem,
                          selected: classes.selectMenuItemSelected,
                        }}
                        value="1"
                      >
                        I'm a Designer
                      </MenuItem>
                      <MenuItem
                        classes={{
                          root: classes.selectMenuItem,
                          selected: classes.selectMenuItemSelected,
                        }}
                        value="2"
                      >
                        I'm a Developer
                      </MenuItem>
                      <MenuItem
                        classes={{
                          root: classes.selectMenuItem,
                          selected: classes.selectMenuItemSelected,
                        }}
                        value="3"
                      >
                        I'm a Hero
                      </MenuItem>
                    </Select>
                  </FormControl>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem
                  md={4}
                  sm={4}
                  className={classNames(
                    classes.mrAuto,
                    classes.mlAuto,
                    classes.textCenter,
                  )}
                >
                  <Button color="primary" round onClick={this.onSubmit.bind(this)} disabled>
                    Let's talk
                  </Button>
                </GridItem>
              </GridContainer>
            </form>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

export default withStyles(contactStyle)(SectionContact);
