import React from 'react';

import AuthUserContext from './context';
import { withFirebase } from '../Firebase';
import Card from 'components/Card/Card';
import CardBody from 'components/Card/CardBody';
import Button from 'components/CustomButtons/Button';
import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';
import withStyles from '@material-ui/core/styles/withStyles';

import appStyle from 'assets/jss/material-dashboard-pro-react/layouts/adminStyle.jsx';


const needsEmailVerification = authUser =>
  authUser &&
  !authUser.emailVerified &&
  authUser.providerData
    .map(provider => provider.providerId)
    .includes('password');

const withEmailVerification = Component => {
  class WithEmailVerification extends React.Component {
    constructor(props) {
      super(props);

      this.state = { isSent: false };
    }

    onSendEmailVerification = () => {
      this.props.firebase
        .doSendEmailVerification()
        .then(() => this.setState({ isSent: true }));
    };

    render() {
      return (
        <AuthUserContext.Consumer>
          {authUser =>
            needsEmailVerification(authUser) ? (
              <div className={this.props.classes.wrapper}>
                <div className={this.props.classes.content}>
                  <div className={this.props.classes.container} style={{justifyContents: 'center'}}>
                    <GridContainer>
                      <GridItem sm={8}>
                        <Card>
                          <CardBody>
                            {this.state.isSent && (
                              <p>
                                E-Mail confirmation sent: Check your E-Mails (Spam
                                folder included) for a confirmation E-Mail.
                                Refresh this page once you have confirmed your E-Mail.
                              </p>
                            )}

                            {!this.state.isSent &&  (
                              <p>
                                Verify your E-Mail: Check your E-Mails (Spam folder
                                included) for a confirmation E-Mail or send
                                another confirmation E-Mail.
                              </p>
                            )}

                            <Button
                              type="button"
                              onClick={this.onSendEmailVerification}
                              disabled={this.state.isSent}
                            >
                              Send confirmation E-Mail
                            </Button>
                          </CardBody>
                        </Card>
                      </GridItem>
                    </GridContainer>
                  </div>
                </div>
                </div>
            ) : (
              <Component {...this.props} />
            )
          }
        </AuthUserContext.Consumer>
      );
    }
  }

  return withStyles(appStyle)(withFirebase(WithEmailVerification));
};

export default withEmailVerification;
