import React from 'react';

import SweetAlert from 'react-bootstrap-sweetalert';

import useGlobal from 'store';

import withStyles from '@material-ui/core/styles/withStyles';
// core components
import Wizard from 'components/Wizard/Wizard.jsx';
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';

import { withFirebase } from 'pages/Firebase';
import buttonStyle from 'assets/jss/material-dashboard-pro-react/components/buttonStyle.jsx';

import { USER_PROFILE_UPDATE } from 'actions/type.js';
import Step1 from './WizardSteps/Step1.jsx';
import Step2 from './WizardSteps/Step2.jsx';
import Step3 from './WizardSteps/Step3.jsx';


const WizardView = ({ firebase, classes }) => {
  const [globalState, globalActions] = useGlobal();
  const showSuccessAlert = globalState.type === USER_PROFILE_UPDATE;

  const hideAlert = () => {
    globalActions.user.hideSuccessAlert();
  };

  const user = globalState.authUser || {};

  return (
    <>
      { showSuccessAlert && (
        <SweetAlert
          success
          style={{ display: 'block', marginTop: '-100px' }}
          title="Updated Successfully!"
          onConfirm={() => hideAlert()}
          onCancel={() => hideAlert()}
          confirmBtnCssClass={
            `${classes.button} ${classes.success}`
          }
        />
      )}
      <GridContainer justify="center">
        <GridItem xs={12} sm={8}>
          <Wizard
            validate
            steps={[
              { stepName: 'Basic Info', stepComponent: withFirebase(Step1), stepId: 'about' },
              { stepName: 'Address', stepComponent: Step3, stepId: 'address' },
            ]}
            title="Build Your Profile"
            subtitle="This information will let us know more about you."
            finishButtonClick={(e) => {
              const {
                about: {
                  first_name,
                  last_name,
                  uid,
                  profileImage,
                },
                address: {
                  street_name,
                  street_no,
                  city,
                  country,
                },
              } = e;

              setTimeout(() => {
                const timestamp = firebase.fieldValue.serverTimestamp();
                firebase.user(uid).set(
                  {
                    first_name,
                    last_name,
                    updatedAt: timestamp,
                    street_name,
                    street_no,
                    city,
                    country,
                  },
                  { merge: true },
                )
                  .then(() => {
                    globalActions.user.updateUser({
                      first_name,
                      last_name,
                      street_name,
                      street_no,
                      city,
                      country,
                      profileImage,
                    });
                  });
              }, 0);
            }}
            initialData={{
              about: {
                first_name: user.first_name,
                last_name: user.last_name,
                email: user.email,
                uid: user.uid,
                profileImage: user.profileImage,
              },
              address: {
                street_name: user.street_name,
                street_no: user.street_no,
                city: user.city,
                country: user.country,
              },
            }}
          />
        </GridItem>
      </GridContainer>
    </>
  );
};

export default withStyles(buttonStyle)(withFirebase(WizardView));
