import React, { useEffect } from 'react';

import classNames from 'classnames';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
// core components
import Footer from 'components/Footer/Footer.jsx';
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import Button from 'components/CustomButtons/Button.jsx';
import Parallax from 'components/Parallax/Parallax.jsx';

import landingPageStyle
  from 'assets/jss/material-kit-pro-react/views/landingPageStyle.jsx';

// Sections for this page
import { PARALLAX_BG_IMG } from 'constants/images.js';
import content from 'components/Footer/content';
import SectionProduct from './Sections/SectionProduct.jsx';
import SectionTeam from './Sections/SectionTeam.jsx';
import SectionWork from './Sections/SectionWork.jsx';
import SectionContacts from './Sections/SectionContacts.jsx';

import { isEmpty } from 'lodash';


const LandingPage = ({
  classes,
  sendMessage,
  handleToggle,
  error,
  checked,
  formData,
}) => {
  useEffect(() => {
    if (window.location.hash !== '#contact-us' && isEmpty(error)) {
      window.scrollTo(0, 0);
      document.body.scrollTop = 0;
    }
  });
  return (
    <>
      <Parallax image={PARALLAX_BG_IMG} filter="dark">
        <div className={classes.container}>
          <GridContainer>
            <GridItem xs={12} sm={6} md={6}>
              <h1 className={classes.title}>Your Story Starts With Us!</h1>
              <h4>
                Every landing page needs a small description after the big
                bold title, that's why we added this text here. Add here all
                the information that can make you or your product create the
                first impression.
              </h4>
              <br />
              <Button
                color="danger"
                size="lg"
                href="https://www.youtube.com/watch?v=dQw4w9WgXcQ"
                target="_blank"
                rel="noopener noreferrer"
              >
                <i className="fas fa-play" />
                Watch video
              </Button>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div className={classes.container}>
          <SectionProduct />
          <SectionTeam />
          {/* <SectionWork /> */}
        </div>
      </div>
      <div className={classes.main}>
        <SectionContacts
          id="contacts"
          sendMessage={sendMessage}
          handleToggle={handleToggle}
          checked={checked}
          error={error}
          formData={formData}
        />
      </div>
      <Footer
        content={content(classes)}
      />
    </>
  );
};

export default withStyles(landingPageStyle)(LandingPage);
