import React from 'react';
import useGlobalHook from 'use-global-hook';

import * as actions from '../actions';

const authUser = localStorage.getItem('authUser');
const initialState = {
  authUser: JSON.parse(authUser),
  users: {
    byIds: {},
    list: [],
  },
  contactUs: {
    imRobot: [1],
  },
  signIn: {
    email: '',
    password: '',
    error: {},
    lastLoginAt: '',
  },
  signUp: {
    first_name: '',
    username: '',
    email: '',
    passwordOne: '',
    passwordTwo: '',
    isAdmin: false,
    error: {},
    agreedTerms: [1],
  },
  messages: {
    byIds: {},
    list: [],
  },
  eventPage: {
    events: [],
    next: null,
    open: false,
    eventData: {},
  },
};

const useGlobal = useGlobalHook(React, { ...initialState }, {
  ...actions,
  root: {
    clear: (store) => {
      store.setState({ type: '', signIn: initialState.signIn, signUp: initialState.signUp });
    },
  },
});

export default useGlobal;
