export const sendMessageFail = (store, payload) => {
  const { contactUs } = store.state;

  store.setState({ contactUs: { ...contactUs, ...payload }, type: CONTACT_US_SEND_MESSAGE_FAIL });
};

export const sendMessage = (store, payload) => {
  const { contactUs } = store.state;

  fetch(`${CLOUD_API_URL}sendMessage`, {
    method: 'Post',
    headers: { 'Content-Type': 'application/json' },
    body: payload,
  })
    .then(() => {
      store.setState({ contactUs: { ...contactUs, error: {} }, type: CONTACT_US_SET_INITAL_STATE });
    })
    .catch((error) => {
      // const errMsg = { email: false, password: false };
      // if (error.code === ERROR_CODE_AUTH_EMAIL_IN_USE) {
      //   errMsg.email = true;
      // }

      // pages.signUp.error = errMsg;
      // store.setState({ pages, type: SIGN_UP_FAIL });
    });
};

export const updateTermsAgreed = (store, payload) => {
  const { contactUs } = store.state;

  store.setState({
    contactUs: { ...contactUs, imRobot: payload },
  });
};
