import app from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';
import 'firebase/storage';

import {
  API_KEY,
  AUTH_DOMAIN,
  DATABASE_URL,
  PROJECT_ID,
  STORAGE_BUCKET,
  MESSAGING_SENDER_ID,
} from 'constants/env';

const config = {
  apiKey: API_KEY,
  authDomain: AUTH_DOMAIN,
  databaseURL: DATABASE_URL,
  projectId: PROJECT_ID,
  storageBucket: STORAGE_BUCKET,
  messagingSenderId: MESSAGING_SENDER_ID,
};

class Firebase {
  constructor() {
    app.initializeApp(config);

    /* Helper */

    this.fieldValue = app.firestore.FieldValue;
    this.emailAuthProvider = app.auth.EmailAuthProvider;

    /* Firebase APIs */
    this.auth = app.auth();
    this.db = app.firestore();
    this.storage = app.storage();

    /* Social Sign In Method Provider */

    this.googleProvider = new app.auth.GoogleAuthProvider();
    this.facebookProvider = new app.auth.FacebookAuthProvider();
    this.twitterProvider = new app.auth.TwitterAuthProvider();
  }

  // *** Auth API ***

  doCreateUserWithEmailAndPassword = (email, password) => this.auth.createUserWithEmailAndPassword(email, password);

  doSignInWithEmailAndPassword = (email, password) => this.auth.signInWithEmailAndPassword(email, password);

  doSignInWithGoogle = () => this.auth.signInWithPopup(this.googleProvider);

  doSignInWithFacebook = () => this.auth.signInWithPopup(this.facebookProvider);

  doSignInWithTwitter = () => this.auth.signInWithPopup(this.twitterProvider);

  doSignOut = () => this.auth.signOut();

  doPasswordReset = email => this.auth.sendPasswordResetEmail(email);

  doSendEmailVerification = () => this.auth.currentUser.sendEmailVerification({
    url: process.env.REACT_APP_CONFIRMATION_EMAIL_REDIRECT,
  });

  doPasswordUpdate = password => this.auth.currentUser.updatePassword(password);

  // *** Merge Auth and DB User API *** //

  onAuthUserListener = (next, fallback) => this.auth.onAuthStateChanged((authUser) => {
    if (authUser) {
      if (authUser.emailVerified) {
        this.user(authUser.uid)
          .get()
          .then((snapshot) => {
            const dbUser = snapshot.data();

            // default empty roles
            if (!dbUser.roles) {
              dbUser.roles = {};
            }
            // TODO: Get Last Login Time Updating
            // this.user(authUser.uid).set(
            //   lastLogin: app.firestore.FieldValue.serverTimestamp(),
            // )
            next({
              uid: authUser.uid,
              email: authUser.email,
              emailVerified: authUser.emailVerified,
              providerData: authUser.providerData,
              ...dbUser,
            });
          });
      } else {
        next(authUser);
      }
    } else {
      fallback();
    }
  });

  // *** User API ***

  user = uid => this.db.doc(`web_users/${uid}`);

  users = () => this.db.collection('web_users');

  // doUpdateLastLogin = () =>
  // this.db.signInWithPopup(t);

  // *** Message API ***

  message = uid => this.db.doc(`web_messages/${uid}`);

  messages = () => this.db.collection('web_messages');

  events = (limit, lastVisible) => {
    let ref = this.db.collection('web_messages');
    // ref = ref.orderBy('date', 'desc');
    if (lastVisible) {
      return ref
        .startAfter(lastVisible)
        .limit(limit);
    }
    return ref.limit(limit);
  };

  uploadMedia = (image) => {
    const { currentUser } = this.auth;
    const metaData = {
      contentType: 'image/png',
      customMetadata: {
        timestamp: image.timestamp ? image.timestamp.toString() : 'none',
        width: image.width ? image.width.toString() : 'none',
        height: image.height ? image.height.toString() : 'none',
        latitude: image.latitude ? image.latitude.toString() : 'none',
        longitude: image.longitude ? image.longitude.toString() : 'none',
      },
    };
    return this.storage
      .ref(`images/${currentUser.uid}/${new Date().getTime()}`)
      .put(image, metaData)
      .then((response) => {
        const url = response.ref.getDownloadURL();
        return url;
      });
  };

  updateProfileImage = (profileImage) => {
    const { uid, email } = this.auth.currentUser;
    this.db.collection('web_users').doc(uid)
      .update({
        email,
        profileImage,
      });
  };

  deleteImage = (url) => {
    return this.storage.ref().child(url).delete();
  }
}

export default Firebase;
