import {
  ADD_EVENT_SUCCESS,
  ADD_EVENT_FAIL,
  UPDATE_EVENT_IMAGE_SUCCESS,
  UPDATE_EVENT_IMAGE_FAIL,
  SET_INITIAL_EVENT_STATE,
  TOGGLE_EVENT_FORM_MODAL,
  UPDATE_EVENT_STATE,
} from 'actions/type';


