export const addEvent = (store, firebase, payload) => {
  const {
    title,
    description,
    userId,
  } = payload;
  firebase.messages()
    .add({
      title,
      description,
      userId,
      createdAt: firebase.fieldValue.serverTimestamp(),
    })
    .then((docRef) => {
      firebase.user(userId)
        .update({
          messages: firebase.fieldValue.arrayUnion(docRef.id),
        });
      const { messages } = store.state;
      if (!messages.byIds[docRef.id]) {
        messages.byIds[docRef.id] = docRef;
        messages.list.push(docRef);
      }
      store.setState({
        type: ADD_EVENT_SUCCESS,
        messages,
      });
    })
    .catch(() => {
      store.setState({ type: ADD_EVENT_FAIL });
    });
};

export const uploadEventImage = (store, firebase, payload) => {
  const { eventId, image } = payload;
  firebase.uploadMedia(image)
    .then((url) => {
      const { messages } = store.state;
      const event = messages.byIds[eventId];
      if (event) {
        event.cover = url;
        store.setState({ type: UPDATE_EVENT_IMAGE_SUCCESS, messages });
      } else {
        store.setState({ type: UPDATE_EVENT_IMAGE_FAIL });
      }
    })
    .catch(() => {
      store.setState({ type: UPDATE_EVENT_IMAGE_FAIL });
    });
};

export const toggleModal = (store) => {
  const { pages } = store.state;

  // pages.eventPage.open = !pages.eventPage.open;
  store.setState({ type: TOGGLE_EVENT_FORM_MODAL, pages });
};

export const update = (store, payload) => {
  const { eventPage } = store.state;

  store.setState({ type: UPDATE_EVENT_STATE, eventPage: { ...eventPage, ...payload } });
};

export const messages = (store, payload) => {
  const { messages } = store.state;

  store.setState({ type: UPDATE_EVENT_STATE, messages: { ...messages, ...payload } });
};
