import React from 'react';

import useGlobal from 'store';
import { SIGN_UP_SUCCESS, SIGN_UP_FAIL, SIGN_UP_UPDATE_AGREED_TERMS } from 'actions/type';
import { withoutAuthentication } from 'pages/Session';
import * as ROLES from 'constants/roles';
import { verifyEmail, setFormValue } from 'utils/form';
import SignUp from './SignupPage';
import * as ROUTES from '../../constants/routes';


const INITIAL_STATE = {
  username: '',
  email: '',
  passwordOne: '',
  passwordTwo: '',
  isAdmin: false,
  error: {},
  agreedTerms: [1],
};

const SignUpPage = ({ firebase, history }) => {
  const [globalState, globalActions] = useGlobal();
  let error = {};
  let user = {};
  let checked = INITIAL_STATE.agreedTerms;
  const { type, signUp } = globalState;

  if (!signUp) {
    // globalActions.user.setSignUpState(INITIAL_STATE);
  } else {
    setTimeout(() => {
      const form = document.querySelector('form');
      if (form) {
        setFormValue(document.querySelector('form'), {
          first_name: signUp.first_name || '',
          email: signUp.email || '',
        });
      }
    }, 0);
  }

  switch (type) {
    case SIGN_UP_SUCCESS:
      history.push(ROUTES.HOME);
      break;
    case SIGN_UP_FAIL:
      error = { ...signUp.error };
      break;
    case SIGN_UP_UPDATE_AGREED_TERMS:
      checked = signUp.agreedTerms || INITIAL_STATE.agreedTerms;
      error = { ...signUp.error };
      break;
    default:
      error = {};
      break;
  }

  const submitHandler = (payload) => {
    const {
      first_name,
      email,
      password,
      isAdmin,
    } = payload;
    if (email && password && first_name && checked.indexOf(1) !== -1 && verifyEmail(email)) {
      const roles = {};

      if (isAdmin) {
        roles[ROLES.ADMIN] = ROLES.ADMIN;
      }
      globalActions.user.doSignUp(
        {
          first_name,
          email,
          password,
          roles,
        },
        firebase,
      );
    } else {
      globalActions.user.signUpFail({
        ...signUp,
        first_name,
        email,
        password,
        error: { first_name: !first_name, email: !verifyEmail(email), password: !password },
      });
    }
  };

  const handleToggle = (value) => {
    const { agreedTerms } = signUp || { ...INITIAL_STATE };
    const currentIndex = agreedTerms.indexOf(value);
    const newChecked = [...agreedTerms];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    globalActions.user.updateTermsAgreed(newChecked);
  };

  const hideAlert = () => {
    globalActions.user.signUpFail({
      ...signUp,
      error: { ...signUp.error, msg: '' },
    });
  };

  return (
    <SignUp
      checked={checked}
      handleToggle={handleToggle}
      onSubmitHandler={submitHandler}
      error={error}
      hideAlert={hideAlert}
      loading={signUp.loader}
    />
  );
};

export default withoutAuthentication(SignUpPage);


