import {
  SIGN_IN_SUCCESS,
  SIGN_IN_FAIL,
  SESSION_CREATE,
  SESSION_REMOVE,
} from 'actions/type';
import { ERROR_CODE_AUTH_USER_NOT_FOUND, ERROR_CODE_AUTH_WRONG_PASSWORD } from 'constants/firebaseErrorCode';

import * as ROUTES from 'constants/routes';


export const setFailState = (store, payload) => {
  const { signIn } = store.state;
  store.setState({ signIn: { ...signIn, ...payload }, type: SIGN_IN_FAIL });
};

export const createSession = (store, authUser) => {
  store.setState({ authUser: { ...store.state.authUser, ...authUser }, type: SESSION_CREATE });
};

export const removeSession = (store) => {
  store.setState({ authUser: null, type: SESSION_REMOVE });
};

export const signIn = (store, payload, firebase) => {
  const { signIn } = store.state;
  const { email, password } = payload;
  const lastLoginAt = firebase.fieldValue.serverTimestamp();
  const page = store.state.signIn;
  store.setState({
    signIn: {
      ...signIn,
      email,
      error: null,
      password,
      loader: true,
    },
  });
  firebase
    .doSignInWithEmailAndPassword(
      email,
      password,
      { lastLoginAt },
    )
    .then((authUser) => {
      const { uid } = authUser.user;
      const { users } = store.state;
      if (!users.byIds[uid]) {
        users.byIds[uid] = authUser;
        users.list.push(users.byIds[uid]);
      }
      store.setState({
        signIn: {
          ...page,
          email,
          error: null,
          lastLoginAt,
          password,
          loader: false,
        },
        type: SIGN_IN_SUCCESS,
        users,
      });
    })
    .catch((error) => {
      const err = { email: false, password: false, msg: '' };
      err.msg = error.message;
      if (error.code === ERROR_CODE_AUTH_WRONG_PASSWORD) {
        err.password = true;
      }
      if (error.code === ERROR_CODE_AUTH_USER_NOT_FOUND) {
        err.email = true;
      }

      signIn.error = err;
      signIn.email = email;
      signIn.loader = false;
      store.setState({ signIn, type: SIGN_IN_FAIL });
    });
};

export const signOut = (store, firebase, history) => {
  firebase
    .doSignOut()
    .then(() => {
      localStorage.removeItem('authUser');
      store.setState({ authUser: null, type: SESSION_REMOVE });
      history.push(ROUTES.SIGN_IN);
    });
};
