import React, { useEffect } from 'react';

import useGlobal from 'store';
import AuthUserContext from './context';
import { withFirebase } from '../Firebase';
import withEmailVerification from 'pages/Session/withEmailVerification';
import { compose } from 'recompose';

const withAuthentication = (Component) => {
  const WithAuthentication = ({ ...props }) => {
    const [globalState, globalActions] = useGlobal();
    const authUser = localStorage.getItem('authUser');
    if (!globalState.authUser && authUser) {
      // globalActions.auth.createSession(JSON.parse(authUser));
    }

    useEffect(() => {
      const listener = props.firebase.onAuthUserListener(
        (authUser) => {
          if (
            !globalState.authUser
            || (
              globalState.authUser
              && (
                globalState.authUser.updatedAt < authUser.updatedAt
                || globalState.authUser.emailVerified !== authUser.emailVerified
              )
            )
          ) {
            globalActions.auth.createSession(authUser);
            localStorage.setItem('authUser', JSON.stringify(authUser));
          }
        },
        () => {
          if (globalState.authUser) {
            localStorage.removeItem('authUser');
            globalActions.auth.removeSession();
          }
        },
      );

      return () => {
        listener();
      };
    });

    return (
      <AuthUserContext.Provider value={globalState.authUser}>
        <Component {...props} />
      </AuthUserContext.Provider>
    );
  };

  return compose(
    withEmailVerification,
    withFirebase,
  )(WithAuthentication);
};

export default withAuthentication;
