import React, { Component } from 'react';

import { AuthUserContext } from '../Session';
import { withFirebase } from '../Firebase';
import MessageList from './MessageList';

// material-ui
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons
import MailOutline from "@material-ui/icons/MailOutline";
// core components
import CardHeaderTypes from './CardImg'
import Card from "components/Card/Card.jsx";
import CardBody from "components/Card/CardBody.jsx";
import CardHeader from "components/Card/CardHeader.jsx";
import CardIcon from "components/Card/CardIcon.jsx";
import CardText from "components/Card/CardText.jsx";

import dashboardStyle from "assets/jss/material-dashboard-pro-react/views/dashboardStyle";
import loginPageStyle from "assets/jss/material-dashboard-pro-react/views/loginPageStyle";

import InputAdornment from "@material-ui/core/InputAdornment";
import People from '@material-ui/icons/People';
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import CustomInput from "components/CustomInput/CustomInput.jsx";

import { Card as CardSUI } from 'semantic-ui-react';

import {
  Message,
  Button,
  Loader,
  Form,
  Icon,
} from 'semantic-ui-react';

var styles = {
  ...dashboardStyle,
  ...loginPageStyle,
  cardTitle: {
    marginTop: "0",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

class Messages extends Component {
  constructor(props) {
    super(props);

    this.state = {
      text: '',
      title: '',
      description: '',
      loading: false,
      messages: [],
      limit: 5
    };
  }

  componentDidMount() {
    this.onListenForMessages();
  }

  onListenForMessages = () => {
    this.setState({ loading: true });

    this.unsubscribe = this.props.firebase
      .messages()
      .orderBy('createdAt', 'desc')
      .limit(this.state.limit)
      .onSnapshot(snapshot => {
        if (snapshot.size) {
          let messages = [];
          snapshot.forEach(doc =>
            messages.push({ ...doc.data(), uid: doc.id }),
          );

          this.setState({
            messages: messages.reverse(),
            loading: false,
          });
        } else {
          this.setState({ messages: null, loading: false });
        }
      });
  };

  componentWillUnmount() {
    this.unsubscribe();
  }

  onChangeText = event => {
    this.setState({ text: event.target.value });
  };

  onChangeTitle = event => {
    this.setState({ title: event.target.value });
  };

  onChangeDescription = event => {
    this.setState({ description: event.target.value });
    console.log(event.target.value)
  };

  onCreateMessage = (event, authUser) => {
    this.props.firebase.messages().add({
      text: this.state.text,
      title: this.state.title,
      description: this.state.description,
      userId: authUser.uid,
      createdAt: this.props.firebase.fieldValue.serverTimestamp(),
    }).then(function(docRef) {
      this.props.firebase.user(authUser.uid).update({
        messages: this.props.firebase.fieldValue.arrayUnion(docRef.id)
      });
    }.bind(this))

    this.setState({ text: '' });
    this.setState({ title: '' });
    this.setState({ description: '' });

    event.preventDefault();
  };

  onEditMessage = (message, text) => {
    const { uid, ...messageSnapshot } = message;

    this.props.firebase.message(message.uid).set({
      ...messageSnapshot,
      text,
      editedAt: this.props.firebase.fieldValue.serverTimestamp(),
    });
  };

  onRemoveMessage = (uid, authUser) => {
    this.props.firebase.user(authUser.uid).update({
      messages: this.props.firebase.fieldValue.arrayRemove(uid)
    });
    this.props.firebase.message(uid).delete();
  };

  onNextPage = () => {
    this.setState(
      state => ({ limit: state.limit + 5 }),
      this.onListenForMessages,
    );
  };

  render() {
    const { text, title, description, messages, loading } = this.state;
    const { classes } = this.props;
    return (
      <AuthUserContext.Consumer>
        {authUser => (
          <CardSUI fluid={true}>
            <CardSUI.Content>
              <CardSUI.Description>
                {loading && <Loader active inline="centered" />}

                {!loading && messages && (
                  <Button
                    size="small"
                    positive
                    type="button"
                    onClick={this.onNextPage}
                  >
                    Load Older Messages
                  </Button>
                )}

                {messages && (
                  <MessageList
                    authUser={authUser}
                    messages={messages}
                    onEditMessage={this.onEditMessage}
                    onRemoveMessage={this.onRemoveMessage}
                  />
                )}

                {!loading && !messages && (
                  <Message info>
                    <p>There are no messages ...</p>
                  </Message>
                )}


                {!loading && (
                    <Card>
                      <CardHeader color="rose" icon>
                        <CardIcon color="rose">
                          <MailOutline />
                        </CardIcon>
                      </CardHeader>

                      <CardBody>
                        {/*Title of Event*/}
                        <CustomInput
                          id="title"
                          inputProps={{
                            placeholder: "Title"
                          }}
                          formControlProps={{
                            fullWidth: true,
                            value: {title},
                            onChange: this.onChangeTitle
                          }}
                        />

                        {/*Description of Event of Event*/}
                        <CustomInput
                          labelText="Description the Event to Your Guests"
                          id="description"
                          formControlProps={{
                            fullWidth: true,
                            value: {description},
                            onChange: this.onChangeDescription
                          }}
                          inputProps={{
                            multiline: true,
                            rows: 5
                          }}
                        />

                        {/*Description of Event of Event*/}
                        <h4 className={classes.cardTitle}>Type your message here</h4>

                        <Form
                          onSubmit={event =>
                            this.onCreateMessage(event, authUser)
                          }
                        >
                          <Form.TextArea
                            value={text}
                            onChange={this.onChangeText}
                            placeholder="Enter your message here..."
                          />

                          <Button primary type="submit">
                            Send <Icon name="send" />
                          </Button>
                        </Form>

                      </CardBody>
                    </Card>
                )}

                {/*<CardHeaderTypes>*/}

              </CardSUI.Description>
            </CardSUI.Content>
          </CardSUI>
        )}
      </AuthUserContext.Consumer>
    );
  }
}

export default withStyles(styles)(withFirebase(Messages));
