import React, { useEffect } from 'react';
// nodejs library that concatenates classes
import classNames from 'classnames';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
// core components
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import Parallax from 'components/Parallax/Parallax.jsx';
import Footer from 'components/Footer/Footer.jsx';
// sections for this page
import SectionDescription from 'pages/AboutUs/Sections/SectionDescription.jsx';
import SectionTeam from 'pages/AboutUs/Sections/SectionTeam.jsx';
import SectionServices from 'pages/AboutUs/Sections/SectionServices.jsx';
import SectionOffice from 'pages/AboutUs/Sections/SectionOffice.jsx';
import SectionContact from 'pages/AboutUs/Sections/SectionContact';

import aboutUsStyle from 'assets/jss/material-kit-pro-react/views/aboutUsStyle.jsx';
import { PARALLAX_BG_IMG_ABOUT_US } from 'constants/images';

import content from 'components/Footer/content';
import useGlobal from 'store';
import { ADMIN_EMAIL, WORK_WITH_US_SUBJECT } from 'constants/env';
import { verifyEmail } from 'utils/form';

const AboutUsPage = ({ classes }) => {
  const [globalState, globalActions] = useGlobal();
  useEffect(() => {
    if (!globalState.type) {
      // window.scrollTo(0, 0);
      // document.body.scrollTop = 0;
    }
  });

  const sendMessage = (payload) => {
    const {
      name,
      email,
      appliedFor,
    } = payload;

    if (name && email && appliedFor && verifyEmail(email)) {
      globalActions.email.sendMessage({
        subject: WORK_WITH_US_SUBJECT,
        mailTo: ADMIN_EMAIL,
        text: `
          name: ${payload.firstName} ${payload.lastName}
          email: ${payload.email}

          message: ${payload.message}
        `,
      });
      // error = {};
      document.querySelector('#contacts form').reset();
    } else {
      // globalActions.email.sendMessageFail({
      //   ...contactUs,
      //   error: {
      //     firstName: !firstName,
      //     email: !email,
      //     lastName: !lastName,
      //     message: !message,
      //   },
      // });
    }
  };

  return (
    <>
      <Parallax image={PARALLAX_BG_IMG_ABOUT_US} filter="dark" small>
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem
              md={8}
              sm={8}
              className={classNames(
                classes.mlAuto,
                classes.mrAuto,
                classes.textCenter,
              )}
            >
              <h1 className={classes.title}>About Us</h1>
              <h4>
                Meet the amazing team behind this project and find out more
                about how we work.
              </h4>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div className={classes.container}>
          <SectionDescription />
          <SectionTeam />
          <SectionServices />
          <SectionOffice />
          <SectionContact
            sendMessage={sendMessage}
          />
        </div>
      </div>
      <Footer
        content={content(classes)}
      />
    </>
  );
}

export default withStyles(aboutUsStyle)(AboutUsPage);
