import React from 'react';

// @material-ui/icons
import Face from '@material-ui/icons/Face';
import RecordVoiceOver from '@material-ui/icons/RecordVoiceOver';
import Email from '@material-ui/icons/Email';

// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import InputAdornment from '@material-ui/core/InputAdornment';

// core components
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import PictureUpload from 'components/CustomUpload/PictureUpload.jsx';
import CustomInput from 'components/CustomInput/CustomInput.jsx';
import { verifyEmail, verifyLength } from 'utils/form';

const style = {
  infoText: {
    fontWeight: '300',
    margin: '10px 0 30px',
    textAlign: 'center',
  },
  inputAdornmentIcon: {
    color: '#555',
  },
  inputAdornment: {
    position: 'relative',
  },
};

class Step1 extends React.Component {
  constructor(props) {
    super(props);
    const { allStates: { about } } = this.props;
    const state = {};
    if (about) {
      state.first_name = about.first_name;
      state.last_name = about.last_name;
      state.email = about.email;
      state.first_nameState = !about.first_name ? 'error' : 'success';
      state.last_nameState = !about.last_name ? 'error' : 'success';
      state.emailState = !about.email ? 'error' : 'success';
      state.profileImage = about.profileImage;
      state.uid = about.uid;
    }
    this.state = state;
  }

  sendState() {
    return this.state;
  }

  change(event, stateName, type, stateNameEqualTo) {
    switch (type) {
      case 'email':
        if (verifyEmail(event.target.value)) {
          this.setState({ [`${stateName}State`]: 'success' });
        } else {
          this.setState({ [`${stateName}State`]: 'error' });
        }
        break;
      case 'length':
        if (verifyLength(event.target.value, stateNameEqualTo)) {
          this.setState({ [`${stateName}State`]: 'success' });
        } else {
          this.setState({ [`${stateName}State`]: 'error' });
        }
        break;
      default:
        break;
    }
    this.setState({ [stateName]: event.target.value });
  }

  isValidated() {
    if (
      this.state.first_nameState === 'success'
      && this.state.last_nameState === 'success'
      && this.state.emailState === 'success'
    ) {
      return true;
    }
    if (this.state.first_nameState !== 'success') {
      this.setState({ first_nameState: 'error' });
    }
    if (this.state.last_nameState !== 'success') {
      this.setState({ last_nameState: 'error' });
    }
    if (this.state.emailState !== 'success') {
      this.setState({ emailState: 'error' });
    }

    return false;
  }

  render() {
    const { classes } = this.props;
    const { email, uid, profileImage } = this.state;
    return (
      <GridContainer justify="center">
        <GridItem xs={12} sm={12}>
          <h4 className={classes.infoText}>
            Let's start with the basic information (with validation)
          </h4>
        </GridItem>
        <GridItem xs={12} sm={4}>
          <PictureUpload
            image={profileImage}
            uploadPicture={(media) => {
              this.props.firebase.uploadMedia(media).then((response) => {
                this.setState({ profileImage: response });
                this.props.firebase.updateProfileImage(response);
              });
            }}
          />
        </GridItem>
        <GridItem xs={12} sm={6}>
          <CustomInput
            success={this.state.first_nameState === 'success'}
            error={this.state.first_nameState === 'error'}
            labelText={(
              <span>
                First Name <small>(required)</small>
              </span>
            )}
            id="first_name"
            formControlProps={{
              fullWidth: true,
            }}
            inputProps={{
              value: this.state.first_name,
              onChange: event => this.change(event, 'first_name', 'length', 1),
              endAdornment: (
                <InputAdornment
                  position="end"
                  className={classes.inputAdornment}
                >
                  <Face className={classes.inputAdornmentIcon} />
                </InputAdornment>
              ),
            }}
          />
          <CustomInput
            success={this.state.last_nameState === 'success'}
            error={this.state.last_nameState === 'error'}
            labelText={(
              <span>
                Last Name <small>(required)</small>
              </span>
            )}
            id="last_name"
            formControlProps={{
              fullWidth: true,
            }}
            inputProps={{
              value: this.state.last_name,
              onChange: event => this.change(event, 'last_name', 'length', 1),
              endAdornment: (
                <InputAdornment
                  position="end"
                  className={classes.inputAdornment}
                >
                  <RecordVoiceOver className={classes.inputAdornmentIcon} />
                </InputAdornment>
              ),
            }}
          />
        </GridItem>
        <GridItem xs={12} sm={12} md={12} lg={10}>
          <CustomInput
            labelText={(
              <span>
                Email <small>(required)</small>
              </span>
            )}
            id="email"
            formControlProps={{
              fullWidth: true,
            }}
            inputProps={{
              disabled: true,
              disableUnderline: true,
              value: this.state.email,
              onChange: event => this.change(event, 'email', 'email'),
              endAdornment: (
                <InputAdornment
                  position="end"
                  className={classes.inputAdornment}
                >
                  <Email className={classes.inputAdornmentIcon} />
                </InputAdornment>
              ),
            }}
          />
        </GridItem>
      </GridContainer>
    );
  }
}

export default withStyles(style)(Step1);
