import {
  SIGN_UP_FAIL,
  SIGN_UP_UPDATE_AGREED_TERMS,
  USER_PROFILE_UPDATE,
  USER_PROFILE_HIDE_SUCCESS_ALERT,
} from 'actions/type';
import { ERROR_CODE_AUTH_EMAIL_IN_USE } from 'constants/firebaseErrorCode';


export const signUpFail = (store, payload) => {
  const { signUp } = store.state;

  store.setState({ signUp: { ...signUp, ...payload }, type: SIGN_UP_FAIL });
};

export const updateTermsAgreed = (store, payload) => {
  const { signUp } = store.state;

  store.setState({
    signUp: { ...signUp, agreedTerms: payload },
    type: SIGN_UP_UPDATE_AGREED_TERMS,
  });
};

export const doSignUp = (store, payload, firebase) => {
  const {
    email,
    password,
    roles,
    first_name,
  } = payload;
  const { signUp } = store.state;
  store.setState({
    signUp: {
      ...signUp,
      email,
      password,
      roles,
      first_name,
      loader: true,
    },
  });
  const timestamp = firebase.fieldValue.serverTimestamp();
  firebase
    .doCreateUserWithEmailAndPassword(email, password)
    .then((authUser) => {
      const { uid } = authUser.user;
      const { users } = store.state;
      if (!users.byIds[uid]) {
        users.byIds[uid] = authUser;
        users.list.push(users.byIds[uid]);
      }
      store.setState({
        users,
        signUp: {
          email: '',
          first_name: '',
          password: '',
          loader: false,
        },
      });
      // Create a user in your Firebase realtime database
      return firebase.user(authUser.user.uid).set(
        {
          username: email,
          first_name,
          email,
          roles,
          createdAt: timestamp,
          lastLoginAt: timestamp,
        },
        { merge: true },
      );
    })
    .then(() => firebase.doSendEmailVerification())
    .catch((error) => {
      const errMsg = { email: false, password: false };

      if (error.code === ERROR_CODE_AUTH_EMAIL_IN_USE) {
        errMsg.email = true;
        errMsg.msg = error.message;
      }

      signUp.error = errMsg;
      store.setState({ signUp: { ...signUp, first_name, email, loader: false }, type: SIGN_UP_FAIL });
    });
};

export const updateUser = (store, payload) => {
  const { authUser } = store.state;
  store.setState({ authUser: { ...authUser, ...payload }, type: USER_PROFILE_UPDATE });
};

export const hideSuccessAlert = (store) => {
  store.setState({ type: USER_PROFILE_HIDE_SUCCESS_ALERT });
};

export const startLoader = (store, payload) => {
  const { user } = store.state;
  store.setState({ user: { ...user, ...payload, loader: true } });
};
