import React, { useEffect } from 'react';

import SweetAlert from 'react-bootstrap-sweetalert';

// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import CircularProgress from '@material-ui/core/CircularProgress';
import InputAdornment from '@material-ui/core/InputAdornment';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Icon from '@material-ui/core/Icon';
// @material-ui/icons
import Timeline from '@material-ui/icons/Timeline';
import Code from '@material-ui/icons/Code';
import Group from '@material-ui/icons/Group';
import Face from '@material-ui/icons/Face';
import Email from '@material-ui/icons/Email';
import Check from '@material-ui/icons/Check';
// core components
import Footer from 'components/Footer/Footer.jsx';
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import Button from 'components/CustomButtons/Button.jsx';
import Card from 'components/Card/Card.jsx';
import CardBody from 'components/Card/CardBody.jsx';
import InfoArea from 'components/InfoArea/InfoArea.jsx';
import CustomInput from 'components/CustomInput/CustomInput.jsx';

import signupPageStyle from 'assets/jss/material-kit-pro-react/views/signupPageStyle.jsx';
import buttonStyle from 'assets/jss/material-dashboard-pro-react/components/buttonStyle.jsx';
import infoStyle from 'assets/jss/material-kit-pro-react/components/infoStyle.jsx';

import { SIGN_IN_BG_IMG } from 'constants/images';
import { FormDataToJSON } from 'utils/form';

import content from 'components/Footer/content';

const SignUpPage = ({
  classes,
  checked,
  handleToggle,
  error,
  onSubmitHandler,
  user,
  hideAlert,
  loading,
}) => {

  useEffect(() => {
    if (!error.first_name && !error.email && !error.password) {
      window.scrollTo(0, 0);
      document.body.scrollTop = 0;
    }
  });

  const onSubmit = (event) => {
    event.preventDefault();
    const payload = FormDataToJSON(event.target);
    onSubmitHandler(payload);
  };

  return (
    <div>
      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: `url(${SIGN_IN_BG_IMG})`,
          backgroundSize: 'cover',
          backgroundPosition: 'top center',
        }}
      >
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={10} md={10}>
              <Card className={classes.cardSignup}>
                <h2 className={classes.cardTitle}>Register</h2>
                <CardBody>
                  <GridContainer justify="center">
                    <GridItem xs={12} sm={5} md={5}>
                      <InfoArea
                        className={classes.infoArea}
                        title="Marketing"
                        description="We've created the marketing campaign of the website. It was a very interesting collaboration."
                        icon={Timeline}
                        iconColor="rose"
                      />
                      <InfoArea
                        className={classes.infoArea}
                        title="Fully Coded in HTML5"
                        description="We've developed the website with HTML5 and CSS3. The client has access to the code using GitHub."
                        icon={Code}
                        iconColor="primary"
                      />
                      <InfoArea
                        className={classes.infoArea}
                        title="Built Audience"
                        description="There is also a Fully Customizable CMS Admin Dashboard for this product."
                        icon={Group}
                        iconColor="info"
                      />
                    </GridItem>
                    <GridItem xs={12} sm={5} md={5}>
                      <div className={classes.textCenter}>
                        <Button justIcon round color="twitter">
                          <i
                            className={`${classes.socials} fab fa-twitter`}
                          />
                        </Button>
                        {' '}
                        <Button justIcon round color="dribbble">
                          <i
                            className={`${classes.socials} fab fa-dribbble`}
                          />
                        </Button>
                        {' '}
                        <Button justIcon round color="facebook">
                          <i
                            className={`${classes.socials} fab fa-facebook-f`}
                          />
                        </Button>
                        {' '}
                        <h4 className={classes.socialTitle}>
                          or be classical
                        </h4>
                      </div>
                      { error.msg && (
                        <SweetAlert
                          customClass={classes.danger}
                          style={{ display: 'block', marginTop: '-100px', color: 'red' }}
                          title="Error in Signup"
                          onConfirm={() => hideAlert()}
                          onCancel={() => hideAlert()}
                          confirmBtnCssClass={
                            `${classes.button} ${classes.info}`
                          }
                        >
                          {error.msg}
                        </SweetAlert>
                      )}
                      <form
                        className={classes.form}
                        onSubmit={onSubmit}
                      >
                        <CustomInput
                          formControlProps={{
                            fullWidth: true,
                            className: classes.customFormControlClasses,
                          }}
                          error={error.first_name}
                          inputProps={{
                            name: 'first_name',
                            startAdornment: (
                              <InputAdornment
                                position="start"
                                className={classes.inputAdornment}
                              >
                                <Face
                                  className={classes.inputAdornmentIcon}
                                />
                              </InputAdornment>
                            ),
                            placeholder: 'First Name...',
                          }}
                        />
                        <CustomInput
                          formControlProps={{
                            fullWidth: true,
                            className: classes.customFormControlClasses,
                          }}
                          error={error.email}
                          inputProps={{
                            name: 'email',
                            startAdornment: (
                              <InputAdornment
                                position="start"
                                className={classes.inputAdornment}
                              >
                                <Email
                                  className={classes.inputAdornmentIcon}
                                />
                              </InputAdornment>
                            ),
                            placeholder: 'Email...',
                          }}
                        />
                        <CustomInput
                          formControlProps={{
                            fullWidth: true,
                            className: classes.customFormControlClasses,
                          }}
                          error={error.password}
                          inputProps={{
                            name: 'password',
                            type: 'password',
                            startAdornment: (
                              <InputAdornment
                                position="start"
                                className={classes.inputAdornment}
                              >
                                <Icon className={classes.inputAdornmentIcon}>
                                  lock_outline
                                </Icon>
                              </InputAdornment>
                            ),
                            placeholder: 'Password...',
                          }}
                        />
                        <FormControlLabel
                          classes={{
                            label: classes.label,
                          }}
                          control={(
                            <Checkbox
                              tabIndex={-1}
                              onClick={() => handleToggle(1)}
                              checkedIcon={
                                <Check className={classes.checkedIcon} />
                              }
                              icon={
                                <Check className={classes.uncheckedIcon} />
                              }
                              classes={{
                                checked: classes.checked,
                                root: classes.checkRoot,
                              }}
                              checked={
                                checked.indexOf(1) !== -1
                              }
                            />
                          )}
                          label={(
                            <span>
                              I agree to the{' '}
                              <a href="#pablo">terms and conditions</a>.
                            </span>
                          )}
                        />
                        <div className={classes.textCenter}>
                          <Button
                            round
                            color="primary"
                            type="submit"
                            disabled={loading}
                          >
                            Get started
                          </Button>
                          {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                      </form>
                    </GridItem>
                  </GridContainer>
                </CardBody>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
        <Footer
          className={classes.footer}
          content={content(classes)}
        />
      </div>
    </div>
  );
};

export default withStyles((theme) => {
  return {
    ...signupPageStyle,
    ...buttonStyle,
    danger: { color: infoStyle.danger },
    buttonProgress: {
      position: 'absolute',
      left: '50%',
      marginTop: 14,
      marginLeft: -12,
    },
  };
})(SignUpPage);
