import React, { Component } from 'react';

import moment from 'moment';
// core components
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import Card from 'components/Card/Card.jsx';
import CardBody from 'components/Card/CardBody.jsx';

import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import Person from '@material-ui/icons/Person';
import Edit from '@material-ui/icons/Edit';
import Close from '@material-ui/icons/Close';

import { withFirebase } from 'pages/Firebase';
import Typography from '@material-ui/core/Typography';
import Button from 'components/CustomButtons/Button';
import EventForm from 'views/Components/EventForm';
import { FormDataToJSON } from 'utils/form';
import ConfirmationDialog from 'views/Components/ConfirmationDialog';
import CardFooter from 'components/Card/CardFooter';

const eventDefaultImage = 'https://www.zamzar.com/images/filetypes/jpg.png';

const styles = {
  eventTitle: {
    fontWeight: 'bold',
  },
  eventDesc: {

  },
  eventImage: {
    height: '100px',
    width: '100px',
    margin: 'auto',
  },
  gridItem: {
    display: 'flex',
  },
  dateWrapper: {
    margin: 'auto',
  },
  dateText: {
    textAlign: 'center',
    marginBottom: '0px',
    marginTop: '0px',
  },
  fab: {
    position: 'fixed',
    bottom: '154px',
    right: '50px',
  },
  wrapper: {
    position: 'relative',
  },
};


const eventTypes = [
  { label: 'Food', iconClass: 'fas fa-hamburger' },
  { label: 'Drinks', iconClass: 'fas fa-beer' },
  { label: 'Music', iconClass: 'fas fa-music' },
  { label: 'Sports', iconClass: 'fas fa-football-ball' },
  { label: 'Festivals', iconClass: 'fas fa-user-friends' },
  { label: 'Social', iconClass: 'fas fa-share-alt' },
  { label: 'Other', iconClass: 'fas fa-ellipsis-h' },
];

class Events extends Component {
  constructor(props) {
    super(props);
    this.state = {
      events: [],
      next: null,
      open: false,
      eventData: {},
      image: null,
      confirmOpen: false,
      selectedEvent: null,
      isEdit: false,
      showLoadMore: true,
      isRemoveImage: false,
    };
  }

  componentDidMount() {
    this.props.firebase.events(10).get()
      .then(this.updateEventList.bind(this));
  }

  handleClickOpen = () => {
    this.setState({
      open: true,
    });
  };

  handleClose = () => {
    this.setState({ open: false, selectedEvent: {}, eventData: {}, isEdit: false });
  };

  handleChange = (evt) => {
    this.setState({ eventData: { ...this.state.eventData, type: evt.target.value }});
  }

  handleSubmit = (evt, place) => {
    const { firebase } = this.props;
    const {
      title,
      date,
      owner,
      type,
      desc,
      end,
      start,
    } = FormDataToJSON(document.querySelector('form'));
    const userId = firebase.auth.currentUser.uid;
    const eventTP = moment(date);
    firebase.messages()
      .add({
        date: eventTP.format('YYYY-MM-DD'),
        title,
        desc,
        userId,
        owner,
        end,
        start,
        type,
        ...place,
        createdAt: firebase.fieldValue.serverTimestamp(),
      })
      .then((docRef) => {
        firebase.user(userId)
          .update({
            messages: firebase.fieldValue.arrayUnion(docRef.id),
          });
        if (this.state.image) {
          this.uploadCover(docRef, true);
        } else {
          const events = this.state.events;
          docRef.get()
            .then((response) => {
              events.push({ ...response.data(), ref: docRef })
              this.setState({ events, open: false, eventData: {}, image: null });
            });
        }
      })
      .catch((e) => {
        console.log(e)
      });
  }

  updateEvent = (evt, place) => {
    const { selectedEvent, eventData, isRemoveImage } = this.state;
    const { firebase } = this.props;
    const ref = selectedEvent.ref || selectedEvent;

    const {
      title,
      date,
      owner,
      type,
      desc,
      end,
      start,
    } = FormDataToJSON(document.querySelector('form'));
    const userId = firebase.auth.currentUser.uid;
    const eventTP = moment(date);
    firebase.messages()
      .doc(ref.id)
      .update({
        date: eventTP.format('YYYY-MM-DD'),
        title,
        desc,
        userId,
        owner,
        end,
        start,
        type,
        ...place,
        createdAt: firebase.fieldValue.serverTimestamp(),
        cover: (isRemoveImage || !eventData.cover) ? null : eventData.cover,
      })
      .then((docRef) => {
        if (this.state.image) {
          this.uploadCover(ref);
        } else {
          const events = this.state.events;

          ref.get()
            .then((response) => {
              this.setState({ events: this.syncEventsAfter(response), open: false, eventData: {}, image: null, isEdit: false });
            });
        }

        if (isRemoveImage) {
          this.props.firebase.deleteImage(eventData.cover);
          this.setState({ isRemoveImage: false });
        }
      })
      .catch((e) => {
        console.log(e)
      });
  }

  uploadCover(docRef, isNewEvent) {
    this.props.firebase.uploadMedia(this.state.image)
      .then((url) => {
        docRef.update({
          cover: url,
        })
          .then(() => {
            docRef.get()
              .then((r) => {
                const events = isNewEvent
                  ? [...this.state.events, { ...r.data(), ref: r }] : this.syncEventsAfter(r);
                this.setState({
                  events,
                  open: false,
                  eventData: {},
                  image: null,
                  isEdit: false,
                });
              });
          })
          .catch((e) => {
            console.log(e)
          });
      })
      .catch((e) => {
        console.log(e)
      });
  }

  loadMore() {
    this.state.next.get().then(this.updateEventList.bind(this));
  }

  syncEventsAfter(updatedEvent) {
    const { events } = this.state;
    events.map((evt) => {
      if (evt.ref.id === updatedEvent.id) {
        const e = updatedEvent.data();
        Object.keys(e).forEach((key) => {
          evt[key] = e[key];
        });
      }
      return evt;
    });

    return events;
  }

  updateEventList(querySnapshot) {
    const events = [...this.state.events];
    const lastVisible = querySnapshot.docs[querySnapshot.docs.length - 1];
    querySnapshot.forEach((doc) => {
      events.push({ ...doc.data(), ref: doc });
    });

    this.setState({ events, next: this.props.firebase.events(10, lastVisible), showLoadMore: lastVisible });
  }

  removeEvent(id) {
    this.props.firebase.messages()
      .doc(id)
      .delete()
      .then(() => {
        let events = this.state.events;
        events = events.filter(event => event.ref.id !== id);
        this.setState({ events });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  render() {
    const {
      open,
      events,
      eventData,
      confirmOpen,
      selectedEvent,
      showLoadMore,
    } = this.state;

    const { auth: { currentUser }} = this.props.firebase;

    const userId = currentUser && currentUser.uid;
    return (
      <>
        <div style={styles.wrapper}>
          <GridContainer>
            <GridItem>
              <GridContainer>
                <GridItem xs={1} sm={1}>Date</GridItem>
                <GridItem xs={2} sm={2} />
                <GridItem xs={7} sm={7}>Description</GridItem>
                <GridItem xs={2} sm={2}>Actions</GridItem>
              </GridContainer>
            </GridItem>
            <GridItem>
              {events.map((event, i) => event.title && (
                <Card
                  key={`${event.title}-${i}`}
                >
                  <CardBody>
                    <GridContainer>
                      <GridItem xs={1} sm={1} style={styles.dateWrapper}>
                        <h3 style={styles.dateText}>{moment(event.date).format('DD')}</h3>
                        <div style={styles.dateText}>{moment(event.date).format('MMM')}</div>
                      </GridItem>
                      <GridItem xs={2} sm={2} style={styles.gridItem}>
                        <img src={event.cover || eventDefaultImage} style={styles.eventImage}/>
                      </GridItem>
                      <GridItem xs={7} sm={7}>
                        <h5
                          style={styles.eventTitle}
                        >
                          {event.title}
                        </h5>
                        <Typography noWrap paragraph>{event.desc}</Typography>
                        {event.address1 && !event.address2 && (
                          <Typography noWrap muted>{`at ${event.address1}`}</Typography>
                        )}
                        {event.address1 && event.address2 && (
                          <Typography noWrap muted>{`at ${event.address1},${event.address2}`}</Typography>
                        )}
                      </GridItem>
                      <GridItem xs={2} sm={2}>
                        <GridContainer>
                          <GridItem sm={3}>
                            {false && (
                              <Button
                                justIcon
                                size="sm"
                                simple
                                color="info"
                                disabled
                              >
                                <Person />
                              </Button>
                            )}
                          </GridItem>
                          <GridItem sm={3}>
                            {userId === event.userId && (
                              <Button
                                justIcon
                                size="sm"
                                simple
                                color="success"
                                onClick={() => {
                                  this.setState({
                                    open: !open,
                                    eventData: event,
                                    isEdit: true,
                                    selectedEvent: event.ref || event,
                                  });
                                }}
                              >
                                <Edit />
                              </Button>
                            )}
                          </GridItem>
                          <GridItem sm={3}>
                            {userId === event.userId && (
                              <Button
                                justIcon
                                size="sm"
                                simple
                                color="danger"
                                onClick={() => {
                                  this.setState({
                                    confirmOpen: !confirmOpen,
                                    selectedEvent: event.ref || event,
                                  });
                                }}
                              >
                                <Close />
                              </Button>
                            )}
                          </GridItem>
                        </GridContainer>
                      </GridItem>
                    </GridContainer>
                  </CardBody>
                </Card>
              ))}
              {events.length > 0 && events.length > 9 && showLoadMore && (
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                  <Button
                    size="sm"
                    color="info"
                    onClick={() => { this.loadMore(); }}
                  >
                    Load More
                  </Button>
                </div>
              )}
            </GridItem>
          </GridContainer>
          <Fab
            color="primary"
            aria-label="Add"
            style={styles.fab}
            onClick={this.handleClickOpen}
          >
            <AddIcon />
          </Fab>
          {open && (
            <EventForm
              formData={eventData}
              open={open}
              handleChange={this.handleChange}
              handleClose={this.handleClose}
              typeList={eventTypes}
              handleSubmit={this.state.isEdit ? this.updateEvent : this.handleSubmit}
              setFile={(image) => {
                if (image) {
                  this.setState({ image });
                } else {
                  this.setState({ isRemoveImage: true });
                }
              }}
              isEdit={this.state.isEdit}
            />
          )}
          <ConfirmationDialog
            title="Delete Event"
            open={confirmOpen}
            body="Are you sure you want to delete this event?"
            onClose={(answer) => {
              if (answer) {
                this.removeEvent(selectedEvent.ref.id);
              }
              this.setState({ confirmOpen: !confirmOpen, selectedEvent: null });
            }}
          />
        </div>
      </>
    );
  }
}

export default withFirebase(Events);
