import React, { Component } from 'react';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
// @material-ui/icons
import PinDrop from '@material-ui/icons/PinDrop';
import Phone from '@material-ui/icons/Phone';
import Check from '@material-ui/icons/Check';
// core components
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import InfoArea from 'components/InfoArea/InfoArea.jsx';
import Card from 'components/Card/Card.jsx';
import CardHeader from 'components/Card/CardHeader.jsx';
import CardBody from 'components/Card/CardBody.jsx';
import CardFooter from 'components/Card/CardFooter.jsx';
import CustomInput from 'components/CustomInput/CustomInput.jsx';
import Button from 'components/CustomButtons/Button.jsx';

import contactsStyle from 'assets/jss/material-kit-pro-react/views/sectionsSections/contactsStyle.jsx';

import city from 'assets/img/examples/city.jpg';
import { FormDataToJSON } from 'utils/form';


class SectionContacts extends Component {
  constructor(props) {
    super(props);
    const { formData, checked } = props;
    this.state = {
      firstName: formData.firstName,
      lastName: formData.lastName,
      email: formData.email,
      message: formData.message,
      checked,
    };
  }

  onSubmit(event) {
    event.preventDefault();
    const payload = FormDataToJSON(event.target);
    this.props.sendMessage(payload);
  }

  change(event, stateName) {
    this.setState({ [stateName]: event.target.value });
  }

  render() {
    const {
      classes,
      sendMessage,
      handleToggle,
      checked,
      error,
    } = this.props;
    const {
      firstName,
      lastName,
      email,
      message,
    } = this.state;

    return (
      <div className="cd-section" {...this.props}>
        {/* Contact us 1 START */}
        <div
          className={`${classes.contacts} ${classes.section}`}
          style={{ backgroundImage: `url(${city})` }}
        >
          <div className={classes.container}>
            <GridContainer>
              <GridItem xs={12} sm={5} md={5}>
                <h2 className={classes.title}>Get in Touch</h2>
                <h5 className={classes.description}>
                  You need more information? Check what other persons are saying
                  about our product. They are very happy with their purchase.
                </h5>
                <InfoArea
                  className={classes.infoArea}
                  title="Find us at the office"
                  description={(
                    <span>
                      Bld Mihail Kogalniceanu, nr. 8,
                      <br /> 7652 Bucharest,
                      <br /> Romania
                    </span>
                  )}
                  icon={PinDrop}
                />
                <InfoArea
                  className={classes.infoArea}
                  title="Give us a ring"
                  description={(
                    <span>
                      Michael Jordan
                      <br /> +40 762 321 762
                      <br /> Mon - Fri, 8:00-22:00
                    </span>
                  )}
                  icon={Phone}
                />
              </GridItem>
              <GridItem xs={12} sm={5} md={5} className={classes.mlAuto}>
                <Card className={classes.card1}>
                  <form
                    onSubmit={this.onSubmit.bind(this)}
                  >
                    <CardHeader
                      contact
                      color="primary"
                      className={classes.textCenter}
                    >
                      <h4 className={classes.cardTitle}>Contact Us</h4>
                    </CardHeader>
                    <CardBody>
                      <GridContainer>
                        <GridItem xs={12} sm={6} md={6}>
                          <CustomInput
                            labelText="First Name"
                            id="first"
                            formControlProps={{
                              fullWidth: true,
                            }}
                            error={error.firstName}
                            inputProps={{
                              name: 'firstName',
                              value: firstName,
                              onChange: event => this.change(event, 'firstName'),
                            }}
                          />
                        </GridItem>
                        <GridItem xs={12} sm={6} md={6}>
                          <CustomInput
                            labelText="Last Name"
                            id="last"
                            error={error.lastName}
                            formControlProps={{
                              fullWidth: true,
                            }}
                            inputProps={{
                              name: 'lastName',
                              value: lastName,
                              onChange: event => this.change(event, 'lastName'),
                            }}
                          />
                        </GridItem>
                      </GridContainer>
                      <CustomInput
                        labelText="Email Address"
                        id="email-address"
                        error={error.email}
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          name: 'email',
                          value: email,
                          onChange: event => this.change(event, 'email'),
                        }}
                      />
                      <CustomInput
                        labelText="Your Message"
                        id="message"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        error={error.message}
                        inputProps={{
                          multiline: true,
                          rows: 5,
                          name: 'message',
                          value: message,
                          onChange: event => this.change(event, 'message'),
                        }}
                      />
                    </CardBody>
                    <CardFooter className={classes.justifyContentBetween}>
                      <FormControlLabel
                        control={(
                          <Checkbox
                            tabIndex={-1}
                            onClick={() => handleToggle(checked.length > 0 ? 1 : -1)}
                            checkedIcon={
                              <Check className={classes.checkedIcon} />
                            }
                            icon={<Check className={classes.uncheckedIcon} />}
                            classes={{
                              checked: classes.checked,
                              root: classes.checkRoot,
                            }}
                            checked={
                              checked.indexOf(1) !== -1
                            }
                          />
                        )}
                        classes={{ label: classes.label }}
                        label="I'm not a robot"
                      />
                      <Button
                        color="primary"
                        className={classes.pullRight}
                        type="submit"
                        disabled
                      >
                        Send Message
                      </Button>
                    </CardFooter>
                  </form>
                </Card>
              </GridItem>
            </GridContainer>
          </div>
        </div>
        {/* Contact us 1 END */}
      </div>
    );
  }
}

export default withStyles(contactsStyle)(SectionContacts);
