import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { Route } from 'react-router-dom';
// creates a beautiful scrollbar
import PerfectScrollbar from 'perfect-scrollbar';
import 'perfect-scrollbar/css/perfect-scrollbar.css';

import 'assets/scss/material-dashboard-pro-react.scss?v=1.5.0';

// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';

// core components
import AdminNavbar from 'components/Navbars/AdminNavbar.jsx';
import Footer from 'components/Footer/afterAuthFooter.jsx';
import Sidebar from 'components/Sidebar/Sidebar.jsx';
// import FixedPlugin from 'components/FixedPlugin/FixedPlugin.jsx';

import routes from 'routes.js';

import appStyle from 'assets/jss/material-dashboard-pro-react/layouts/adminStyle.jsx';

import image from 'assets/img/default-avatar.png';

import logo from 'assets/img/logo-white.svg';

import { withEmailVerification, withAuthorization } from 'pages/Session';

import useGlobal from 'store';
import { compose } from 'recompose';

let ps;


const HomeLayout = (Component) => {
  const [globalState] = useGlobal();

  class Dashboard extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        mobileOpen: false,
        miniActive: false,
        image,
        color: 'blue',
        bgColor: 'black',
        // hasImage: true,
        fixedClasses: 'dropdown',
      };
      this.resizeFunction = this.resizeFunction.bind(this);
      if (!globalState.authUser) {
        this.props.history.push('/signin');
      }
    }

    componentDidMount() {
      if (navigator.platform.indexOf('Win') > -1) {
        ps = new PerfectScrollbar(this.refs.mainPanel, {
          suppressScrollX: true,
          suppressScrollY: false,
        });
        document.body.style.overflow = 'hidden';
      }
      window.addEventListener('resize', this.resizeFunction);
    }

    componentDidUpdate(e) {
      if (e.history.location.pathname !== e.location.pathname) {
        this.refs.mainPanel.scrollTop = 0;
        if (this.state.mobileOpen) {
          this.setState({ mobileOpen: false });
        }
      }
    }

    componentWillUnmount() {
      if (navigator.platform.indexOf('Win') > -1) {
        ps.destroy();
      }
      window.removeEventListener('resize', this.resizeFunction);
    }

    handleImageClick = (image) => {
      this.setState({ image });
    };

    handleColorClick = (color) => {
      this.setState({ color });
    };

    handleBgColorClick = (bgColor) => {
      this.setState({ bgColor });
    };

    handleFixedClick = () => {
      if (this.state.fixedClasses === 'dropdown') {
        this.setState({ fixedClasses: 'dropdown show' });
      } else {
        this.setState({ fixedClasses: 'dropdown' });
      }
    };

    handleDrawerToggle = () => {
      this.setState({ mobileOpen: !this.state.mobileOpen });
    };

    getRoute() {
      return this.props.location.pathname !== '/home/full-screen-maps';
    }

    getActiveRoute = (routes) => {
      const activeRoute = 'Dashboard';
      for (let i = 0; i < routes.length; i++) {
        if (routes[i].collapse) {
          const collapseActiveRoute = this.getActiveRoute(routes[i].views);
          if (collapseActiveRoute !== activeRoute) {
            return collapseActiveRoute;
          }
        } else if (
          window.location.href.indexOf(routes[i].layout + routes[i].path) !== -1
        ) {
          return routes[i].name;
        }
      }
      return activeRoute;
    };

    getRoutes = routes => routes.map((prop, key) => {
      if (prop.collapse) {
        return this.getRoutes(prop.views);
      }
      if (prop.layout === "/home") {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      }
      return null;
    });

    sidebarMinimize() {
      this.setState({ miniActive: !this.state.miniActive });
    }

    resizeFunction() {
      if (window.innerWidth >= 960) {
        this.setState({ mobileOpen: false });
      }
    }

    render() {
      const { classes, ...rest } = this.props;
      const mainPanel = `${classes.mainPanel
      } ${
        cx({
          [classes.mainPanelSidebarMini]: this.state.miniActive,
          [classes.mainPanelWithPerfectScrollbar]:
            navigator.platform.indexOf('Win') > -1,
        })}`;
      return (
        <div className={classes.wrapper}>
          {globalState.authUser && <Sidebar
            routes={routes}
            logoText="Outlit Pro"
            logo={logo}
            image={this.state.image}
            handleDrawerToggle={this.handleDrawerToggle}
            open={this.state.mobileOpen}
            color={this.state.color}
            bgColor={this.state.bgColor}
            miniActive={this.state.miniActive}
            {...rest}
            activeUser={globalState.authUser}
          />}
          <div className={mainPanel} ref="mainPanel">
            <AdminNavbar
              sidebarMinimize={this.sidebarMinimize.bind(this)}
              miniActive={this.state.miniActive}
              brandText={this.getActiveRoute(routes)}
              handleDrawerToggle={this.handleDrawerToggle}
              {...rest}
            />
            {/* On the /maps/full-screen-maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}
            {/* {this.getRoute() ? (
              <div className={classes.content}>
                <div className={classes.container}>
                  <Switch>{this.getRoutes(routes)}</Switch>
                </div>
              </div>
            ) : (
              <div className={classes.map}>
                <Switch>{this.getRoutes(routes)}</Switch>
              </div>
            )} */}
            <div className={classes.content}>
              <div className={classes.container}>
                {Component && <Component />}
              </div>
            </div>
            {this.getRoute() ? <Footer fluid /> : null}
            {/* <FixedPlugin
              handleImageClick={this.handleImageClick}
              handleColorClick={this.handleColorClick}
              handleBgColorClick={this.handleBgColorClick}
              handleHasImage={this.handleHasImage}
              color={this.state.color}
              bgColor={this.state.bgColor}
              bgImage={this.state.image}
              handleFixedClick={this.handleFixedClick}
              fixedClasses={this.state.fixedClasses}
              sidebarMinimize={this.sidebarMinimize.bind(this)}
              miniActive={this.state.miniActive}
            /> */}
          </div>
        </div>
      );
    }
  }

  Dashboard.propTypes = {
    classes: PropTypes.object.isRequired,
  };

  const condition = authUser => !!authUser;

  return withStyles(appStyle)(
    compose(
      withEmailVerification,
      withAuthorization(condition),
    )(Dashboard),
  );
};

export default HomeLayout;
