import React, { Component, useEffect } from 'react';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import InputAdornment from '@material-ui/core/InputAdornment';
import CircularProgress from '@material-ui/core/CircularProgress';
import Icon from '@material-ui/core/Icon';
// @material-ui/icons
import Email from '@material-ui/icons/Email';
// core components
import Footer from 'components/Footer/Footer.jsx';
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';
import Button from 'components/CustomButtons/Button.jsx';
import Card from 'components/Card/Card.jsx';
import CardBody from 'components/Card/CardBody.jsx';
import CardHeader from 'components/Card/CardHeader.jsx';
import CustomInput from 'components/CustomInput/CustomInput.jsx';

import loginPageStyle from 'assets/jss/material-kit-pro-react/views/loginPageStyle.jsx';
import buttonStyle from 'assets/jss/material-dashboard-pro-react/components/buttonStyle.jsx';
import infoStyle from 'assets/jss/material-kit-pro-react/components/infoStyle.jsx';

import { SIGN_IN_BG_IMG } from 'constants/images';

import { FormDataToJSON } from 'utils/form';

import content from 'components/Footer/content';
import SweetAlert from 'react-bootstrap-sweetalert';

const LoginPage = ({ classes, onSubmitHandler, error, hideAlert, loading }) => {

  const onSubmit = (event) => {
    event.preventDefault();
    const payload = FormDataToJSON(event.target);
    onSubmitHandler(payload);
  };

  return (
    <div
      className={classes.pageHeader}
      style={{
        backgroundImage: `url(${SIGN_IN_BG_IMG})`,
        backgroundSize: 'cover',
        backgroundPosition: 'top center',
      }}
    >
      { error.msg && (
        <SweetAlert
          customClass={classes.danger}
          style={{ display: 'block', marginTop: '-100px', color: 'red' }}
          title="Error in Login"
          onConfirm={() => hideAlert()}
          onCancel={() => hideAlert()}
          confirmBtnCssClass={
            classes.button + " " + classes.info
          }
        >
          {error.msg}
        </SweetAlert>
      )}
      <div className={classes.container}>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={4}>
            <Card>
              <form
                className={classes.form}
                onSubmit={onSubmit}
              >
                <CardHeader
                  color="primary"
                  signup
                  className={classes.cardHeader}
                >
                  <h4 className={classes.cardTitle}>Login</h4>
                  <div className={classes.socialLine}>
                    <Button
                      justIcon
                      color="transparent"
                      className={classes.iconButtons}
                      onClick={e => e.preventDefault()}
                    >
                      <i className="fab fa-twitter" />
                    </Button>
                    <Button
                      justIcon
                      color="transparent"
                      className={classes.iconButtons}
                      onClick={e => e.preventDefault()}
                    >
                      <i className="fab fa-facebook" />
                    </Button>
                    <Button
                      justIcon
                      color="transparent"
                      className={classes.iconButtons}
                      onClick={e => e.preventDefault()}
                    >
                      <i className="fab fa-google-plus-g" />
                    </Button>
                  </div>
                </CardHeader>
                <p
                  className={`${classes.description} ${classes.textCenter}`}
                >
                  Or Be Classical
                </p>
                <CardBody signup>
                  {/* <CustomInput
                    id="first"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: 'First Name...',
                      type: 'text',
                      startAdornment: (
                        <InputAdornment position="start">
                          <Face className={classes.inputIconsColor} />
                        </InputAdornment>
                      ),
                    }}
                  /> */}
                  <CustomInput
                    id="email"
                    error={error.email}
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: 'Email...',
                      type: 'email',
                      startAdornment: (
                        <InputAdornment position="start">
                          <Email className={classes.inputIconsColor} />
                        </InputAdornment>
                      ),
                      name: 'email',
                    }}
                  />
                  <CustomInput
                    id="pass"
                    error={error.password}
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      placeholder: 'Password',
                      type: 'password',
                      startAdornment: (
                        <InputAdornment position="start">
                          <Icon className={classes.inputIconsColor}>
                            lock_utline
                          </Icon>
                        </InputAdornment>
                      ),
                      name: 'password',
                    }}
                  />
                </CardBody>
                <div className={classes.textCenter}>
                  <Button
                    simple
                    color="primary"
                    size="lg"
                    type="submit"
                    disabled={loading}
                  >
                    Get started
                  </Button>
                  {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                </div>
              </form>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
      <Footer
        className={classes.footer}
        content={content(classes)}
      />
    </div>
  );
};

export default withStyles((theme) => {
  const style = loginPageStyle(theme);
  return {
    ...style,
    ...buttonStyle,
    danger: { color: infoStyle.danger },
    buttonProgress: {
      position: 'absolute',
      left: '50%',
      marginTop: 20,
      marginLeft: -12,
    },
  };
})(LoginPage);
