import React from 'react';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';

import AboutUsPage from 'pages/AboutUs';
import LandingPage from 'pages/Landing';
import SignUpPage from 'pages/SignUp';
import SignInPage from 'pages/SignIn';
import PricingPage from 'pages/Pricing';
import ProductPage from 'pages/Product';
// import PasswordForgetPage from 'pages/PasswordForget';
import BaseLayout from 'layouts/BaseLayout';
import HomeLayout from 'layouts/HomeLayout';
import HomePage from 'pages/Home';
import AccountPage from 'pages/Account';
import ProfilePage from 'views/Pages/UserProfile';
import EditProfilePage from 'views/Pages/EditProfile';
import AdminPage from 'pages/Admin';

import Calendar from 'views/Calendar/Calendar.jsx';

import CreateEvent from 'pages/CreateEvent';

import * as ROUTES from 'constants/routes';
import { withAuthentication, withEmailVerification } from 'pages/Session';
import Events from 'views/Pages/Events';
import Event from 'pages/Event/Event';


const App = () => (
  <Router>
    <>
      <Route exact path={ROUTES.LANDING} component={BaseLayout(LandingPage)} />
      <Route path={ROUTES.PRODUCTS} component={BaseLayout(ProductPage)} />
      <Route path={ROUTES.PRICING} component={BaseLayout(PricingPage)} />
      <Route path={ROUTES.ABOUT_US} component={BaseLayout(AboutUsPage)} />
      <Route path={ROUTES.SIGN_UP} component={BaseLayout(SignUpPage)} />
      <Route path={ROUTES.SIGN_IN} component={BaseLayout(SignInPage)} />
      {/* <Route
        path={ROUTES.PASSWORD_FORGET}
        component={PasswordForgetPage}
      /> */}
      <Route path={ROUTES.HOME} component={HomeLayout()} />
      <Route path={ROUTES.CREATE_EVENT} component={CreateEvent} />
      <Route path={ROUTES.ACCOUNT} component={AccountPage} />
      <Route path={ROUTES.PROFILE} component={HomeLayout(ProfilePage)} />
      <Route path={ROUTES.EDIT_PROFILE} component={HomeLayout(EditProfilePage)} />
      {/* <Route path={ROUTES.ADMIN} component={AdminPage} /> */}
      <Route path={ROUTES.EVENTS} component={HomeLayout(Events)} />
      {/* <Route path={ROUTES.EVENT} component={BaseLayout(Event)} /> */}
      <Route path={ROUTES.CALENDAR} component={HomeLayout(Calendar)} />
    </>
  </Router>
);

export default withAuthentication(App);
