import React, { useEffect } from 'react';

import useGlobal from 'store';
import { SIGN_IN_SUCCESS, SIGN_IN_FAIL } from 'actions/type';
import { withoutAuthentication } from 'pages/Session';
import { verifyEmail, setFormValue } from 'utils/form';
import SignIn from './SignIn';
import * as ROUTES from '../../constants/routes';


const SignInPage = ({ firebase, history }) => {
  const [globalState, globalActions] = useGlobal();
  useEffect(() => {
    if (!globalState.type) {
      window.scrollTo(0, 0);
      document.body.scrollTop = 0;
    }
  });
  let error = {};
  const { type, signIn } = globalState;
  if (signIn) {
    setTimeout(() => {
      const form = document.querySelector('form');
      if (form) {
        setFormValue(document.querySelector('form'), {
          email: signIn.email || '',
        });
      }
    }, 0);
  }
  switch (type) {
    case SIGN_IN_SUCCESS:
      // history.push(ROUTES.HOME);
      break;
    case SIGN_IN_FAIL:
      error = { ...signIn.error };
      break;
    default:
      error = {};
      break;
  }

  const submitHandler = (payload) => {
    if (payload.email && payload.password && verifyEmail(payload.email)) {
      globalActions.auth.signIn(payload, firebase);
    } else {
      globalActions.auth.setFailState({
        ...signIn,
        email: payload.email,
        error: { email: !(payload.email && verifyEmail(payload.email)), password: !payload.password },
      });
    }
  };

  const hideAlert = () => {
    globalActions.auth.setFailState({
      ...signIn,
      error: { ...signIn.error, msg: '' },
    });
  };

  return (
    <SignIn
      onSubmitHandler={submitHandler}
      error={error}
      hideAlert={hideAlert}
      loading={signIn.loader}
    />
  );
};

export default withoutAuthentication(SignInPage);

