import React from 'react';
import { compose, withProps, lifecycle } from 'recompose';
import {
  withScriptjs,
} from 'react-google-maps';
import { StandaloneSearchBox } from 'react-google-maps/lib/components/places/StandaloneSearchBox';
import { GOOGLE_MAP_URL } from 'constants/env';
import CustomInput from 'components/CustomInput/CustomInput';

const PlacesWithStandaloneSearchBox = compose(
  withProps({
    googleMapURL: `${GOOGLE_MAP_URL}&libraries=geometry,drawing,places`,
    loadingElement: <div style={{ height: '100%' }} />,
    containerElement: <div style={{ height: '400px', display: 'flex' }} />,
  }),
  lifecycle({
    componentWillMount() {
      const refs = {};

      this.setState({
        places: [],
        onSearchBoxMounted: (ref) => {
          refs.searchBox = ref;
        },
        onPlacesChanged: () => {
          const places = refs.searchBox.getPlaces();
          console.log(places, this.props);
          this.props.onPlaceSelect(places[0]);
          this.setState({
            places,
          });
        },
        onFocus: (event) => {
          const pc = document.querySelector('.pac-container');
          // if (event) {
            const css = `
              .pac-container {
                left: auto !important;
                top: 30px !important;
              }
              .pac-item {
                cursor: pointer;
              }
            `;
            const head = document.head || document.getElementsByTagName('head')[0];
            const style = document.createElement('style');

            head.appendChild(style);

            style.type = 'text/css';
            if (style.styleSheet) {
              // This is required for IE8 and below.
              style.styleSheet.cssText = css;
            } else {
              style.appendChild(document.createTextNode(css));
            }
            event.target.parentNode.appendChild(pc);
          // }
        },
      });
    },
  }),
  withScriptjs
)(props => (
  <div
    data-standalone-searchbox=""
    style={{
      position: 'relative',
    }}
  >
    <StandaloneSearchBox
      ref={props.onSearchBoxMounted}
      bounds={props.bounds}
      onPlacesChanged={props.onPlacesChanged}
    >
      <CustomInput
        labelText={(
          <span>
            Location <small>*</small>
          </span>
        )}
        type="text"
        formControlProps={{
          fullWidth: true,
        }}
        defaultValue={props.value}
        inputProps={{
          onFocus: props.onFocus,
          value: props.value,
        }}
      />
    </StandaloneSearchBox>
  </div>
));

export default PlacesWithStandaloneSearchBox;
