import React, { useEffect } from 'react';
import LandingPage from 'pages/Landing/LandingPage';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import landingPageStyle from 'assets/jss/material-kit-pro-react/views/landingPageStyle.jsx';

import useGlobal from 'store';
import { CONTACT_US_SEND_MESSAGE_FAIL, CONTACT_US_SEND_MESSAGE_SUCCESS } from 'actions/type';
import { CONTACT_US_SUBJECT, ADMIN_EMAIL } from 'constants/env';
import { setFormValue, verifyEmail } from 'utils/form';

const INITIAL_STATE = {
  imRobot: [1],
};

const Landing = () => {
  const [globalState, globalActions] = useGlobal();
  let error = {};
  let checked = INITIAL_STATE.imRobot;
  const { type, contactUs } = globalState;
  if (!contactUs) {
    // globalActions.email.setContactUsState(INITIAL_STATE);
  } else {
    checked = contactUs.imRobot;
  }

  switch (type) {
    case CONTACT_US_SEND_MESSAGE_FAIL:
      window.location.hash = '#contact-us';
      error = { ...contactUs.error };
      break;
    default:
      error = {};
      break;
  }

  const sendMessage = (payload) => {
    const {
      firstName,
      lastName,
      email,
      message,
    } = payload;
    window.location.hash = '#contact-us';

    if (firstName && lastName && email && message && verifyEmail(email) && checked.length > 0) {
      globalActions.email.sendMessage({
        subject: CONTACT_US_SUBJECT,
        mailTo: ADMIN_EMAIL,
        text: `
          name: ${payload.firstName} ${payload.lastName}
          email: ${payload.email}

          message: ${payload.message}
        `,
      });
      error = {};
      document.querySelector('#contacts form').reset();
    } else {
      globalActions.email.sendMessageFail({
        ...contactUs,
        firstName,
        email,
        lastName,
        message,
        error: {
          firstName: !firstName,
          email: !email || !verifyEmail(email),
          lastName: !lastName,
          message: !message,
        },
      });
    }
  };

  const handleToggle = (value) => {
    const { imRobot } = contactUs || { ...INITIAL_STATE };
    const currentIndex = imRobot.indexOf(value);
    const newChecked = [...imRobot];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    globalActions.email.updateTermsAgreed(newChecked);
  };

  return (
    <LandingPage
      sendMessage={sendMessage}
      handleToggle={handleToggle}
      checked={checked}
      error={error}
      formData={contactUs}
    />
  );
};

export default withStyles(landingPageStyle)(Landing);
